//Manejo de rutas navegacion
import { BrowserRouter as Router, Route, Routes, useLocation } from 'react-router-dom'

//rutas
import Error404 from 'containers/errors/Error404';
import Home from 'containers/pages/Home';
import Articles from 'containers/pages/articles/Articles';
import Publish_Articles from 'containers/pages/articles/Create_Articles';

import { AnimatePresence } from 'framer-motion'

function AnimatedRoutes(){
  const location = useLocation()

    return( 
        <AnimatePresence>

                <Routes location={location} key={location.pathname}>
                  {/* Error Display */}
                  <Route path="*" element={<Error404/>}/>

                  {/* Home Display */}
                  <Route path="/" element={<Home/>}/>
                  <Route path="/Explorar" element={<Articles/>}/>
                  <Route path="/Create" component={<Publish_Articles/>} />

                  {/* <Route path="/Explorar/publish/:slug" element={<Publish_Articles/>}/> */}


                </Routes>
        </AnimatePresence>
                
    )
}
export default AnimatedRoutes
