import axios from 'axios';

// Definir tipos de acciones
export const CREATE_ARTICLE_SUCCESS = 'CREATE_ARTICLE_SUCCESS';
export const CREATE_ARTICLE_FAILURE = 'CREATE_ARTICLE_FAILURE';

// Accin para crear un artculo
export const createArticle = (articleData) => {
  return async (dispatch) => {
    try {
      // Realizar la solicitud POST al backend para crear el artculo
      const response = await axios.post('/api/dish/create', articleData);

      if (response.status === 201) {
        // La creacin del artculo fue exitosa
        dispatch({ type: CREATE_ARTICLE_SUCCESS });
      } else {
        // Manejar otros posibles cdigos de estado o errores
        dispatch({ type: CREATE_ARTICLE_FAILURE, error: 'Error al crear el artculo' });
      }
    } catch (error) {
      // Manejar errores de red u otros errores
      dispatch({ type: CREATE_ARTICLE_FAILURE, error: 'Error de red' });
    }
  };
};
