import 'boxicons'

import { Typewriter } from "react-simple-typewriter";
import { useState, useEffect } from 'react';
import { motion, useAnimation } from 'framer-motion';
import DotLoader from 'react-spinners/DotLoader';

const features = [
  {
    name: 'Sendero Del Sabor',
    description: 'Desde tu local favorito, hasta la puerta de tu casa. Descubre cmo conectamos a los amantes de la comida con los sabores locales.',
  },
  {
    name: 'Regstrate como Vendedor',
    description: 'nete a nuestra comunidad. Registra tu local y comienza a vender tus delicias sin costo alguno.',
  },
  {
    name: 'Explora Locales',
    description: 'Navega a travs de una variedad de los locales. Encuentra tus sabores favoritos.',
  },
  {
    name: 'Categoras',
    description: 'Explora platos deliciosos organizados en categoras para una mejor experienca.',
  },
  { 
    name: 'Rpido y Fcil',
    description: 'Personaliza tu pedido segn tus preferencias. Sin complicaciones, solo buenos sabores.',
  },
  {
    name: 'Comunicacin Directa',
    description: 'Agrega tus platos al carrito, confirma la compra y tus detalles se envan automticamente al vendedor.',
  },
  {
    name: 'Notificaciones en Tiempo Real',
    description: 'Recibe notificaciones instantneas sobre el estado de tu compra, ofertas especiales y actualizaciones de tus locales favoritos.',
    
  },
  {
    name: 'Explora el Futuro',
    description: 'Estamos creciendo y mejorando constantemente. Mantente atento a nuevas caractersticas y sorpresas emocionantes que vendrn prximamente.',
  },
]

export default function SecctionWelcome() {

  const [loading, setLoading] = useState(true);
  const [isVisible, setIsVisible] = useState(false);
  const controls = useAnimation();

  useEffect(() => {
    const handleScroll = () => {
      const section = document.querySelector('.bg-amber-50'); // Selecciona la seccin por su clase CSS o cualquier otro selector

      // Verifica si la parte superior de la seccin es visible en la ventana
      if (section.getBoundingClientRect().top <= window.innerHeight && section.getBoundingClientRect().bottom >= 0) {
        setIsVisible(true);
      } else {
        setIsVisible(false);
      }
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  useEffect(() => {
    if (isVisible) {
      controls.start({ opacity: 1, y: 0 });
    } else {
      controls.start({ opacity: 0, y: 100 });
    }
  }, [isVisible, controls]);
  
  return (
    <div className="bg-amber-50">
      <motion.div
      initial={{ opacity: 1, y: 100 }}
      animate={controls}
      transition={{ duration: 1 }}
      className="mx-auto max-w-7xl py-16 px-4 sm:px-6 lg:py-24 lg:px-8"
    >
      <div className="mx-auto max-w-7xl py-16 px-4 sm:px-6 lg:py-24 lg:px-8">
        <div className="mx-auto max-w-3xl text-center">
          <h2 className="text-3xl font-bold text-black tracking-wide uppercase">
            BIENVENIDO A NUESTRA PLATAFORMA
          </h2>
        </div>
        <dl className="mt-12 space-y-10 sm:grid sm:grid-cols-2 sm:gap-x-6 sm:gap-y-12 sm:space-y-0 lg:grid-cols-4 lg:gap-x-8">
          {features.map((feature) => (
            <div key={feature.name} className="relative">
              <div className="absolute flex items-center justify-center h-12 w-12 rounded-md bg-yellow-300 text-white">
                <box-icon name="check" animation="flashing" color="#0c0c0c"></box-icon>
              </div>
              <dt>
                <p className="ml-16 text-lg font-medium leading-6 text-gray-900">
                  {feature.name}
                </p>
              </dt>
              <dd className="mt-2 ml-16 text-base text-gray-500">
                {feature.description}
              </dd>
            </div>
          ))}
        </dl>
      </div>
    </motion.div>
    </div>
  );
}
